.input-block {
  display: inline-block;
  position: relative;
  padding-top: 0px;
  width: 100%;
  margin-bottom: 30px; }
  .input-block label {
    position: absolute;
    top: 16px;
    left: 0;
    font-family: "helvetica", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
    opacity: 0.5;
    transition: 0.3s;
    transition-property: top, font-size, line-height; }
    .input-block label.active {
      font-size: 13px;
      line-height: 24px;
      top: -7px; }
  .input-block input {
    background-color: #ffffff;
    border: 1px solid transparent;
    border-radius: 5px;
    box-shadow: none;
    color: #343A40;
    font-family: "helvetica", sans-serif;
    font-weight: 400;
    font-size: 21px;
    line-height: 22px;
    padding: 14px 21px 12px;
    transition: 0.3s;
    position: relative;
    z-index: 2;
    transition-property: background-color;
    width: 100%; }
    .input-block input:focus {
      background-color: rgba(255, 255, 255, 0.7); }
      .input-block input:focus + label {
        font-size: 13px;
        line-height: 24px;
        top: -7px; }
  .input-block .message {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    padding-top: 0px;
    color: #DC3545;
    font-weight: 300;
    font-size: 14px;
    line-height: 20px;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s linear;
    z-index: 1; }
  .input-block.error input {
    border: 1px solid #DC3545; }
  .input-block.error .message {
    visibility: visible;
    opacity: 1; }
  @media (max-width: 560px) {
    .input-block input {
      padding-bottom: 11px;
      font-size: 16px;
      line-height: 18px; }
    .input-block label {
      font-size: 16px;
      line-height: 18px; } }

